.pagination {
    display: flex;
    justify-content: center;
    margin-top: 24px;
  }
  
  .pagination__item {
    cursor: pointer;
    padding: 8px 16px;
    border-radius: 4px;
    margin-right: 8px;
    background-color: #f5f5f5;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .pagination__item:hover {
    background-color: #e0e0e0;
  }
  
  .pagination__item.active {
    background-color: #007bff;
    color: #fff;
  }
  