.sideBar {
  background-color: #04040a00;
  height: calc(50vh - 50px);
  width: 350px;
  position: fixed;
  left: 0px;
  top: 50px;
  font-family: 'Onest', sans-serif;
  font-weight: 400;
 

  &__header {
      margin-left: 12.5px;
      margin-top: 33px;
      &__text {
          display: inline-block;
          font-size: 24px;
          color: #343434;
          font-weight: bold;
      }
      &__reset {
          display: inline-block;
          position: absolute;
          top: 41px;
          right: 20px;
          font-size: 16px;
          color: #2fafd8;
          cursor: pointer;
      }
      margin-bottom: 33px;
  }
  &__area {
      margin-left: 12.5px;
      margin-right: 20px;
      margin-bottom: 10px;
      &__text {
          font-size: 16px;
          color: #343434;
          font-weight: bold;
          margin-bottom: 10px;
      }
      &__select {
          font-size: 16px;
          color: #343434;
          font-weight: bold;
      }
      &__input {
          &__container {
              height: 42px;
              border: 1px solid #e4e5e7;
              background-color: #fafafb;
              border-radius: 9px;
              justify-content: left;
              display: flex;
          }
          &__text {
              line-height: 42px;
              margin-left: 25px;
              margin-right: 6px;
              font-family: 'Onest', sans-serif;
              font-size: 18px;
              font-weight: 400;
              color: #c7cacf;
          }
          &__element {
              font-family: 'Onest', sans-serif;
              font-size: 18px;
              font-weight: 400;
              width: 100%;
              outline: none;
              border: none;
              background-color: #00000000;
          }
      }
  }
}