.main {
  margin-left: 390px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.message {
  position: absolute;
  top: 135px;
  left: 395px;
  width: calc(100vw - 432px);
  &__text {
      display: block;
      font-family: 'Onest', sans-serif;
      color: #9d9ca0;
      font-size: 18px;
      font-weight: 400;
      text-align: center;
      margin-left: 50%;
      transform: translateX(-50%);
  }
}

.search {
  display: flex;
  width: calc(100vw - 432px);
  position: absolute;
  top: 77px;
  left: 395px;
  min-width: 725px;

  &__input {
      flex-grow: 1;
      margin-right: 10px;
      height: 42px;
      outline: none;
      border: 1px solid #e4e4e4;
      background-color: #FAFAFB;
      padding: 0 20px 0 60px;
      font-family: 'Onest', sans-serif;
      border-radius: 9px;
      color: #343434;
      font-size: 18px;
      font-weight: 400;
      &::placeholder {
          color: #5c5d5f;
          font-weight: 400;
      }
  }

  &__button {
      min-width: 100px;
      height: 42px;
      width: 150px;
      background-color: #62ADC5;
      border: 1px solid #4C90A6;
      border-radius: 9px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: all linear 50ms;

      &:hover {
          filter: brightness(80%);
      }

      &__text {
          color: #FFFFFF;
          font-family: 'Onest', sans-serif;
          font-size: 18px;
          font-weight: 450;
          line-height: 42px;
      }
  }
  &__total {
      position: absolute;
      top: 60px;
      color: #A6A8AB;
      font-family: 'Onest', sans-serif;
      font-size: 16px;
      font-weight: 450;
  }
}

.vacancies {
  position: absolute;
  display: block;
  min-width: 725px;
  width: calc(100vw - 432px);
  top: 156px;
  left: 395px;
}